body{
    padding-right: 0px !important;
}
.organismos{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap:wrap;
}
.organismos a{
    width: calc((100% - 20px) / 3);
    margin: 0 calc(20px / 6);
}
.puntero{
    cursor: pointer;
}
.mas{
    background-color: var(--gold);
    transition: all 0.3s ease-in-out;
    border-radius: 5px;
    padding: 5px 15px;
    color: #fff;
}
.mas:hover{
    background-color: var(--wine);
    color: #fff;
}
.acor_normateca .mas{
  margin-left: 5px;
  cursor: pointer;
}
.comite_objetivo{
  text-align: justify;
}
