.titulo_app{
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 30px;
}
.img_app{
    width: 100%;
}
.img_app img{
    width: 100%;
    margin-bottom: 30px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.img_app img:hover{
  filter: saturate(130%);
}

.tabla_organismos{
  width: 100%;
  border-top: 1px hidden;
  border-bottom: 1px hidden;
}
.tabla_organismos tr{
  border-left: 1px hidden;
  border-right: 1px hidden;
  transition: var(--all);
}
.tabla_organismos td{
  border: 1px solid var(--gold);
  padding: 5px;
}
.acor_normateca{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-bottom: 15px;
  border-bottom: 1px solid #EAEAEA;
  margin-bottom: 15px;
  font-size: 14px;
}

.flex_info{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap:wrap;
}
.de_flex_info{
    width: 48%;
}
.titulo_info{
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.linea_info{
    width: 100%;
    height: 1px;
    background-color: var(--gold);
}
.linea_info::before{
    content: " ";
    width: 35px;
    height: 2.5px;
    background-color: var(--wine) !important;
    display: block;
    position: absolute;
}
.temas_info{
    margin: 8px 0 10px 0;
}
.temas_info li{
    margin-top: 2px;
    cursor: pointer;
}
.temas_info li:hover{
    color: var(--wine);
}
.temas_info li a{
    color: #000;
}
.temas_info li a:hover{
    color: var(--wine);
}
#buzon-trigger{
    z-index: 5 !important;
}
.modal_titulo{
    font-size: 15px;
}
.flex_trimestral{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.flex_trimestral trimestre{
    font-size: 13px;
    width: 24.5%;
    text-align: center;
}
.flex_trimestral trimestre:nth-child(2), .flex_trimestral trimestre:nth-child(3), .flex_trimestral a:nth-child(2), .flex_trimestral a:nth-child(3){
    margin: 0 0.25%;
}
.flex_anual a, .flex_trimestral a{
    width: 100%;
    border-radius: 5px;
    padding: 5px 10px;
    border: 0.5px solid gray;
    margin-top: 3px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}
.flex_trimestral a{
    width: 24.5%;
}
.flex_anual a:hover, .flex_trimestral a:hover{
    background-color: var(--wine);
    color: #fff;
}
.flex_anual a div, .flex_trimestral a div{
    color: #fff;
    background-color: var(--gold);
    border-radius: 5px;
    padding: 2px 5px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
}

@media (max-width:991px) {
    .organismos{
        flex-flow: column;
    }
    .organismos a{
        width: 100%;
        margin: 0;
    }
}
@media (max-width:900px) {
    .flex_info{
        flex-flow: column;
    }
    .de_flex_info{
        width: 100%;
    }
}
@media (max-width:500px) {
    .quita_extension, h90{
        display: none;
    }
    .flex_trimestral a{
        text-align: center;
        justify-content: center;
    }
}
@media (max-width:400px) {
    .flex_trimestral a{
        font-size: 10px;
    }
}